import React, { useState, useContext, useEffect } from "react";
import { useParams } from "react-router-dom";
import Player from '@vimeo/player';

import { AuthContext } from "../../auth/AuthContext";
import * as S from "./styles";
import firebase from "firebase"
import CardCompartilhar from "../../components/CardCompartilhar";

// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'
import Store from "../../Connections/store";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory, useLocation } from "react-router-dom";
import Share from '../../components/CardCompartilhar/index'
let tempo = 0

function Start() {



    setInterval(() => {
        tempo = tempo + 1
        console.log(tempo)
    }, 1000)


};


export default function ComponenteVideo(props) {


    const history = useHistory()
    const [mostar, setMostrar] = useState(false)
    const [visualicacao, setvisualicacao] = useState(0)
    const [compartilhar, setcompartilhar] = useState(false)
    const params = useParams('/playespetaculo/:id/:id2/:id3')
    const { id, id2, id3 } = params


    const [um, setUm] = useState(null)
    const [dois, setdois] = useState(null)
    const [tres, setTres] = useState(null)
    const [quatro, setquatro] = useState(null)
    const [cinco, setcinco] = useState(null)

    let location = useLocation();

    
    

    //total de fedbaak
    const [total, setTotal] = useState(null)

    function fidback(um, dois, tres, quatro, cinco) {

        if (um) {
            setUm(true)
            setdois(false)
            setTres(false)
            setquatro(false)
            setcinco(false)
            setTotal(um)
        }

        if (dois) {
            setUm(true)
            setdois(true)
            setTres(false)
            setquatro(false)
            setcinco(false)
            setTotal(dois)
        }
        if (tres) {
            setUm(true)
            setdois(true)
            setTres(true)
            setquatro(false)
            setcinco(false)
            setTotal(tres)
        }
        if (quatro) {
            setUm(true)
            setdois(true)
            setTres(true)
            setquatro(true)
            setcinco(false)
            setTotal(quatro)
        }

        if (cinco) {
            setUm(true)
            setdois(true)
            setTres(true)
            setquatro(true)
            setcinco(true)
            setTotal(cinco)
        }

    }

    useEffect(() => {


        setTimeout(() => {
            setvisualicacao(1)
        }, 10000)

        //cria o player
    if(location.pathname.substr(0, 15) == "/playespetaculo") {
        
        var options = {
            width: 640,
            loop: true
        };

        var iframe = document.querySelector('iframe');
    
        var player = new Player(iframe, options);
    
        // player.setVolume(0);
    
        // player.on('play', function() {
        //     console.log('played the video!');
        // });

        
        // player.on('playing', function(data) {
        //     console.log(data)
        // });

        setInterval(() => {

            player.on('playing', function(data) {
                player.getCurrentTime().then(function(seconds) {
                    localStorage.setItem(`@App:${id}/${id2}`, JSON.stringify(seconds));
                    // seconds = the current playback position
                }).catch(function(error) {
                    console.log(error)
                    // an error occurred
                });
            });
           
            player.on('ended', function(data) {
                history.push("/")
            });
        }, 500)

        const storagedUser = localStorage.getItem(`@App:${id}/${id2}`);

        if (storagedUser) {

            player.ready().then(function() {
                player.setCurrentTime(JSON.parse(storagedUser)).then(function(seconds) {
                    // seconds = the actual time that the player seeked to
                    console.log(seconds)
                }).catch(function(error) {
                    switch (error.name) {
                        case 'RangeError':
                            // the time was less than 0 or greater than the video’s duration
                            break;
                
                        default:
                            // some other error occurred
                            break;
                    }
                });
            });
       
            

        }
 
    }

    }, [])

    const logado = localStorage.getItem("logado");

    if (logado != 'true') {
        history.push('/login')
    }


    return (
        <div style={{ width: '100vw', display: "flex", padding: 0 }}>

            <S.div >



                <S.Span size="35" style={{ cursor: "pointer" }} onClick={() => history.push('/')}><i className="bi bi-arrow-left"></i></S.Span>
                <S.Span>Visualizaçoes: {visualicacao}</S.Span>
                <S.Span size="30" style={{ cursor: "pointer" }} ><i onClick={() => compartilhar ? setcompartilhar(false) : setcompartilhar(true)} className="bi bi-share"></i> {compartilhar && <Share link={`https://palcoweb.com.br/#/cardezao/${id3}`}></Share>}</S.Span>


                <div>
                    <S.Span size="15" className="m-2">Gostou? de sua nota:  </S.Span>
                    <S.Estrelinhas seleacionado={um ? '#FDCC12' : false}>
                        <i className="bi bi-star-fill" onClick={() => fidback(20, null, null, null)}></i>
                    </S.Estrelinhas>


                    <S.Estrelinhas seleacionado={dois ? '#FDCC12' : false} onClick={() => fidback(null, 40, null, null)}>
                        <i className="bi bi-star-fill"></i>
                    </S.Estrelinhas>


                    <S.Estrelinhas seleacionado={tres ? '#FDCC12' : false} onClick={() => fidback(null, null, 60, null)}>
                        <i className="bi bi-star-fill"></i>
                    </S.Estrelinhas>
                    <S.Estrelinhas seleacionado={quatro ? '#FDCC12' : false} onClick={() => fidback(null, null, null, 80)}>
                        <i className="bi bi-star-fill"></i>
                    </S.Estrelinhas>
                    <S.Estrelinhas seleacionado={cinco ? '#FDCC12' : false} onClick={() => fidback(null, null, null, null, 100)}>
                        <i className="bi bi-star-fill"></i>
                    </S.Estrelinhas>

                </div>

            </S.div>

            <iframe 
                src={`https://player.vimeo.com/video/${id}?h=${id2}`}
                frameborder="0" 
                allow="autoplay; fullscreen; picture-in-picture" 
                allowfullscreen 
                title="Cad&amp;ecirc;.mp4"
                style={{ position: "absolute", marginTop: '0px', padding: 0, width: '100vw' , height: '100vh' }}
            >

            </iframe>

            {/* <iframe id="myvideo"
                src={`https://iframe.dacast.com/vod/${id}/${id2}`}
                style={{ position: "absolute", marginTop: '0px', padding: 0, width: '100%' , height: '800px' }}
                allow="autoplay"
                onClick={()=>alert("oi")}

            >


            </iframe>


            
                
                <ReactNetflixPlayer src="https://www.c-span.org/video/standalone/?c4944572/house-impeachment-managers-video-compilation-january-6-attack-us-capitol" /> */}
                
            


        </div>
    )
}