import React, { useState, useContext, useEffect } from "react";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";

import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
import * as SCard from "../Canais/styles";
import { AuthContext } from "../../auth/AuthContext";
import Layout from '../../layout'

//share
import Share from '../../components/CardCompartilhar/index'

// import Store from "../../Connections/store"

import lista from '../Home/lista'
import ListaCanais from '../Home/listaCanais'
import instagram from '../../assets/instagram-yellow.png';
import youtube from '../../assets/youtube-yellow.png';
import linkedin from '../../assets/linkedin-yellow.png';
import tiktok from '../../assets/tiktok-yellow.png';
import facebook from '../../assets/facebook-yellow.png';
import twitter from '../../assets/twitter-yellow.png';
import listaCanais from "../Home/listaCanais";

function FichaCanal(props) {
  const ListaTeste = lista
  const listarCanais = ListaCanais
  const [visible, setVisible] = useState({ status: false, text: "" });

  const [abriu, setAbri] = useState(false)
  const [abrirCard, setAbrirCardzao] = useState(false)
  const history = useHistory()
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);
  const { currentUser } = useContext(AuthContext);




  //pegando canaiss pela url
  const [canais, setcanais] = useState({})
  const params = useParams('/FichaCanal/:id')
  const { id } = params

  useEffect(

    () => {

      window.scrollTo(0, 0);
      listarCanais.map((canais) => {
        if (canais.key == id) {
          setcanais(canais)
        }
      })
    }
    , [])



  function showConteudo() {
    if (abrirCard) {
      setAbrirCardzao(false)


    } else {

      setAbrirCardzao(true)

    }

  }

  function showHorario() {
    if (abriu) {
      setAbri(false)
    } else {
      setAbri(true)
    }
  }

  function fechar() {
    setAbri(false)
  }
  window.scrollTo(0, 0);


  const logado = localStorage.getItem("logado");



  return (
    <Layout>
      {(
        <>
          {abrirCard ? (
            <>
            </>
          ) : (
            <>

              <S.Container>
                <S.ContainerImg>

                  <S.Thumbnail src={canais.thumbnail} ></S.Thumbnail>


                </S.ContainerImg>

                <S.Conteudo>
                  <S.ButtonVoltar onClick={() => history.push("/canais") || window.location.reload()}>
                    {" "}
                    <i className="bi bi-x-lg"></i>{" "}
                  </S.ButtonVoltar>

                  <S.HeaderConteudo>


                    <S.Item >
                      <S.Coracao>
                        <i className="bi bi-suit-heart"></i>
                      </S.Coracao>

                      <S.Compartilhar >



                        {" "}

                        <i className="bi bi-share-fill" onClick={() => setcompartilharFicha(true)} style={{cursor:"pointer"}}></i>
                        {compartilharFica && <Share link={`http://www.palcoweb.com.br/FichaCanal/${canais.key}`} sairficha={setcompartilharFicha}></Share>}
                      </S.Compartilhar>


                    </S.Item>
                    <S.Img
                      src={canais.thumbnail}
                      style={{
                        margin: 0,
                        width: 150,
                        height: 150,
                        borderRadius: "50%",
                      }}
                      value={canais.key}
                      alt="imagem referente ao canal"
                    />
                    <S.Item>
                      <S.HeaderTitle> <S.Span size="1">{canais.nome}</S.Span></S.HeaderTitle>
                      <div>
                        {/*redes sociais */}
                        {canais.facebook && <a href={canais.facebook} target="_blank" rel="noopener noreferrer" style={{color:'#fdcc12',textDecorationLine:'none'}} target='blank'> <img src={facebook} alt="logo do facebook" /></a>}
                        {canais.instagram && <a href={'https://www.instagram.com/'+canais.instagram} target="_blank" rel="noopener noreferrer"  style={{color:'#fdcc12',textDecorationLine:'none'}} target='blank'> <img src={instagram} alt="logo do instagram" /></a>}
                      </div>
                        {canais.site && <S.Span cor="#fdcc12" size="1" style={{ cursor: "pointer" }}> <a href={canais.site} target="_blank" rel="noopener noreferrer" style={{color:'#fdcc12',textDecorationLine:'none'}}>Site Oficial</a> </S.Span>}
                    </S.Item>


                  </S.HeaderConteudo>


                  <S.Informacoes>



                    <S.Item>
                      <S.Text size="0.8"> {canais.descricao}</S.Text>
                    </S.Item>
                    <S.Item>
                      <S.Span size="0.8"><i class="bi bi-circle-fill p-1"></i>  {canais.categoria}</S.Span>
                    </S.Item>



                  </S.Informacoes>
                </S.Conteudo>
              </S.Container>

              <Section title="em alta" filter>
                <HorizontalList id="emAlta">
                  {ListaTeste.map((card, index) => {
                  
                    if (card.canaisKey === canais.key) {
                      return (
                      <SwiperSlide key={String(index)}>
                        {card === "" ? (
                          <Card noContent />
                        ) : (
                          <S.CardTest>
                            <S.Img
                              src={card.thumbnail}
                              style={{
                                position: "absolute",
                                width: 250,
                                height: 400,
                              }}
                              value={card.key}
                            />

                            <S.InfoCard>
                              <S.InfoCardConteudo>
                                <S.ClassificacaoCard>
                                  {card.classificacao}
                                </S.ClassificacaoCard>
                                <S.Coracao>
                                  <i className="bi bi-suit-heart-fill"></i>
                                </S.Coracao>

                                <S.Compartilhar>
                                  <i className="bi bi-share-fill" onMouseEnter={() => setcompartilhar(true)}></i>
                                  {compartilhar && <Share sair={setcompartilhar}></Share>}
                                </S.Compartilhar>
                              </S.InfoCardConteudo>

                              <S.Titulo>{card.title}</S.Titulo>
                              {/* <S.ConteudoCard>
                                Com {card.Artista}
                              </S.ConteudoCard> */}
                              {/* <S.ConteudoCard>
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                              </S.ConteudoCard> */}
                              <S.ConteudoCard style={{ textTransform: "uppercase" }} >
                                {card.categoria}
                              </S.ConteudoCard>
                              <S.Sinopsia>{card.synopsis}</S.Sinopsia>


                              <S.ButtonAcessar
                                className="btn btn-warning"
                                onClick={() => {

                                  history.push(`/cardezao/${card.key}`)
                                  window.location.reload()
                                }

                                }
                              >
                                Saiba Mais{" "}
                              </S.ButtonAcessar>
                            </S.InfoCard>
                          </S.CardTest>
                        )}
                      </SwiperSlide>
                    );
                    } else {
                      return false
                    }
                    
                  })}
                </HorizontalList>
              </Section>
              {/*}
                    <Section title="Loja" filter>
                        <HorizontalList id="canais">
                            {ListaTeste.map((card, index) => {
                                return (
                                    <SwiperSlide key={String(index)}>
                                        {card === "" ? (
                                            <Card noContent />
                                        ) : (
                                            <S.Card >
                                                <S.Img src={'https://images.pexels.com/photos/7562644/pexels-photo-7562644.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'} style={{ position: "absolute", width: 250, height: 250, }} value={card.key} />
                                            </S.Card>
                                        )}
                                    </SwiperSlide>
                                );
                            })}
                        </HorizontalList>
                    </Section>
                        {*/}

              {/* <S.Produtora>
                <S.ImgProdutora
                  src={require("../../assets/logo-chaim.png")}
                ></S.ImgProdutora>
                <S.Item>
                  Chaim Entretenimento <br /> A Chaim Entretenimento é
                  responsável por promo- ver entretenimento de alta qualidade
                  e proporcio- nar ao público grandes espetáculos teatrais e
                  musi- cais. Com 20 anos de experiência, produzindo e
                  promovendo diversos espetáculos por todo o Brasil.
                </S.Item>
                <S.Item>CONHEÇA O CANAL </S.Item>
                <S.Item className="m-1">
                  {" "}
                  <i class="bi bi-chevron-right"></i>{" "}
                </S.Item>
              </S.Produtora> */}

            </>
          )}
        </>
      )}
    </Layout>
  );
}

export default FichaCanal;
