import React, { useState, useContext, useEffect } from "react";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import * as S from "./styles";
import { AuthContext } from "../../auth/AuthContext";
import Layout from '../../layout';
import banner1 from '../../assets/banner2.png';
//share
import Share from '../../components/CardCompartilhar/index';

// import Store from "../../Connections/store"
import TelaCardezao from '../Cardezao/index';
import lista from '../Home/lista';
import Horarios from "../Horarios";
import ComponenteVideo from '../../pages/ComponenteVideo';
import ListaHok from '../../dados/hooks/listaHok'
function Cardezao(props) {
  const ListaTeste = lista
  const [visible, setVisible] = useState({ status: false, text: "" });

  const [abriu, setAbri] = useState(false)
  const [abrirCard, setAbrirCardzao] = useState(false)
  const history = useHistory()
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);
  const { currentUser } = useContext(AuthContext);
  const listaHok = ListaHok()




  //pegando espetaculos pela url
  const [espetaculo, setespetaculo] = useState({})
  const params = useParams('/cardezao/:id')
  const { id } = params
  const Datas = new Date()
  const [dia, setDia] = useState(0)
  const [mes, setmes] = useState(0)
  const [ano, setano] = useState(0)
  const [horas, sethoras] = useState(0)
  const [dataSelecionada, setdataSelecionada] = useState({})
 



  console.log("dia: " + dia + " Mes: " + mes + " Ano:" + ano + " horas:" + horas)
  useEffect(

    () => {

      window.scrollTo(0, 0);
      ListaTeste.map((espetaculo) => {
        if (espetaculo.key == id) {
          setespetaculo(espetaculo)
        }
      })



      setdataSelecionada({
        dia:Datas.getDate(),
        mes:Datas.getMonth() + 1,
        ano:Datas.getFullYear(),
        horas:Datas.getHours(),
      })

    }


    , [])



  function showConteudo() {
    if (abrirCard) {
      setAbrirCardzao(false)


    } else {

      setAbrirCardzao(true)

    }

  }

  function showHorario() {
    if (abriu) {
      setAbri(false)
    } else {
      setAbri(true)
    }
  }

  function fechar() {
    setAbri(false)
  }
  window.scrollTo(0, 0);


  const logado = localStorage.getItem("logado");

  function proximo() {

    history.push(`/LoginCart/id`);

  }





  return (
    <Layout>
      {abriu ? (
        <Horarios
          aidKey={espetaculo.key}
          link={espetaculo.link}
          direcao={espetaculo.direcao}
          producao={espetaculo.producao}
          elenco={espetaculo.elenco}
          voltar={fechar}
          sinopsiaEsp={espetaculo.synopsis}
          descricaoClassifica={espetaculo.descricaoClassifica}
          classificacao={espetaculo.classificacao}
          img={espetaculo.thumbnail}
          tituloEsp={espetaculo.title}
          artistaEsp={espetaculo.Artista}
          categoria={espetaculo.categoria}
          datas={espetaculo.datas}
          hosrios={espetaculo.datas.horarios}
          videoID={espetaculo.videoID}
          videoID2={espetaculo.videoID2}
        ></Horarios>
      ) : (
        <>
          {abrirCard ? (
            <>
            </>
          ) : (
            <>

              <S.Container>
                <S.ContainerImg>



                  <iframe

                    src={`${espetaculo.tailher}`}
                    allow="autoplay"
                    allowfullscreen
                    allowfullscreen="true"
                    webkitallowfullscreen="true"
                    mozallowfullscreen="true"

                  >

                  </iframe>





                </S.ContainerImg>

                <S.Conteudo>
                  <S.ButtonVoltar onClick={() => history.push("/") || window.location.reload()}>
                    {" "}
                    <i className="bi bi-x-lg"></i>{" "}
                  </S.ButtonVoltar>

                  <S.HeaderConteudo>
                    <S.Item>
                      <S.Span>

                        <S.Classificacao>
                          {espetaculo.classificacao}
                        </S.Classificacao>{" "}
                        <S.Span className="m-2">
                          {espetaculo.descricaoClassifica}
                        </S.Span>

                      </S.Span>
                    </S.Item>


                    <S.Item >
                      <S.Coracao>
                        <i className="bi bi-suit-heart-fill"></i>
                      </S.Coracao>

                      <S.Compartilhar >
                        {" "}

                        <i className="bi bi-share-fill" onMouseEnter={() => setcompartilharFicha(true)}></i>
                        {compartilharFica && <Share link={`http://www.palcoweb.com.br/cardezao/${espetaculo.key}`} sairficha={setcompartilharFicha}></Share>}
                      </S.Compartilhar>
                    </S.Item>
                  </S.HeaderConteudo>


                  <S.Informacoes>
                    <S.Item>
                      <S.HeaderTitle> {espetaculo.title}</S.HeaderTitle>
                    </S.Item>
                    <S.Item>
                      <S.Span> Com {espetaculo.Artista}</S.Span>
                    </S.Item>
                    <S.Item>
                      {/* <S.Text>
                        {" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>{" "}
                        <i className="bi bi-star-fill"></i>
                        <i className="bi bi-star-fill"></i>
                      </S.Text> */}


                      {/* {

                        <S.ButtonAcessar className="btn btn-warning m-5" onClick={() => {
                          if (currentUser) {
                            history.push(`/playespetaculo/${espetaculo.videoID}/${espetaculo.videoID2}/${espetaculo.key}`)
                          } else {
                            history.push("/login")
                          }
                        }
                        }>ASSISTIR</S.ButtonAcessar>
                      } */}

                    </S.Item>
                    <S.Item>
                      <S.Text> {espetaculo.synopsis}</S.Text>
                    </S.Item>
                    <S.Item>
                      <S.Span> Elenco: {espetaculo.elenco}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> producao: {espetaculo.producao}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> direcao: {espetaculo.direcao}</S.Span>
                    </S.Item>
                    <S.Item>
                      <S.Span> duração: {espetaculo.duracao}</S.Span>
                    </S.Item>




                    <S.Item>

                      <S.ButtonContinuar
                        className="btn btn-warning"
                        onClick={() => {
                          proximo()
                          listaHok.AdicionarEspetaculos({
                            key: espetaculo.key,
                            valorDia: '',
                            valorHorarioSe:dataSelecionada ,
                            valorPreco: 9,
                            link: espetaculo.link,
                            direcao: espetaculo.direcao,
                            producao: espetaculo.producao,
                            elenco: espetaculo.elenco,
                            artista: espetaculo.Artista,
                            sinopsiaEsp: espetaculo.synopsis,
                            descricaoClassifica: espetaculo.descricaoClassifica,
                            classificacao: espetaculo.classificacao,
                            thumbnail: espetaculo.thumbnail,
                            tituloEsp: espetaculo.title,
                            artistaEsp: espetaculo.Artista,
                            categoria: espetaculo.categoria,
                            videoID: espetaculo.videoID,
                            videoID2: espetaculo.videoID2,
                            trailher: espetaculo.tailher
                          })
                        }}
                      >
                        Continuar
                      </S.ButtonContinuar>
                    </S.Item>
                  </S.Informacoes>
                </S.Conteudo>
              </S.Container>
              <Section title="em alta" filter>
                <HorizontalList id="emAlta">
                  {ListaTeste.map((card, index) => {
                    return (
                      <SwiperSlide key={String(index)}>
                        {card === "" ? (
                          <Card noContent />
                        ) : (
                          <S.CardTest>
                            <S.Img
                              src={card.thumbnail}
                              style={{
                                position: "absolute",
                                width: 250,
                                height: 400,
                              }}
                              value={card.key}
                            />

                            <S.InfoCard>
                              <S.InfoCardConteudo>
                                <S.ClassificacaoCard>
                                  {card.classificacao}
                                </S.ClassificacaoCard>
                                <S.Coracao>
                                  <i className="bi bi-suit-heart-fill"></i>
                                </S.Coracao>

                                <S.Compartilhar>
                                  <i className="bi bi-share-fill" onMouseEnter={() => setcompartilhar(true)}></i>
                                  {compartilhar && <Share sair={setcompartilhar}></Share>}
                                </S.Compartilhar>
                              </S.InfoCardConteudo>

                              <S.Titulo>{card.title}</S.Titulo>
                              {/* <S.ConteudoCard>
                                Com {card.Artista}
                              </S.ConteudoCard> */}
                              {/* <S.ConteudoCard>
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>{" "}
                                <i className="bi bi-star-fill"></i>
                                <i className="bi bi-star-fill"></i>
                              </S.ConteudoCard> */}
                              <S.ConteudoCard style={{ textTransform: "uppercase" }} >
                                {card.categoria}
                              </S.ConteudoCard>
                              <S.Sinopsia>{card.synopsis}</S.Sinopsia>


                              <S.ButtonAcessar
                                className="btn btn-warning"
                                onClick={() => {

                                  history.push(`/cardezao/${card.key}`)
                                  window.location.reload()
                                }

                                }
                              >
                                Saiba Mais{" "}
                              </S.ButtonAcessar>
                            </S.InfoCard>
                          </S.CardTest>
                        )}
                      </SwiperSlide>
                    );
                  })}
                </HorizontalList>
              </Section>
              {/*}
                    <Section title="Loja" filter>
                        <HorizontalList id="canais">
                            {ListaTeste.map((card, index) => {
                                return (
                                    <SwiperSlide key={String(index)}>
                                        {card === "" ? (
                                            <Card noContent />
                                        ) : (
                                            <S.Card >
                                                <S.Img src={'https://images.pexels.com/photos/7562644/pexels-photo-7562644.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940'} style={{ position: "absolute", width: 250, height: 250, }} value={card.key} />
                                            </S.Card>
                                        )}
                                    </SwiperSlide>
                                );
                            })}
                        </HorizontalList>
                    </Section>
                        {*/}

              {/* <S.Produtora>
                <S.ImgProdutora
                  src={require("../../assets/logo-chaim.png")}
                ></S.ImgProdutora>
                <S.Item>
                  Chaim Entretenimento <br /> A Chaim Entretenimento é
                  responsável por promo- ver entretenimento de alta qualidade
                  e proporcio- nar ao público grandes espetáculos teatrais e
                  musi- cais. Com 20 anos de experiência, produzindo e
                  promovendo diversos espetáculos por todo o Brasil.
                </S.Item>
                <S.Item>CONHEÇA O CANAL </S.Item>
                <S.Item className="m-1">
                  {" "}
                  <i class="bi bi-chevron-right"></i>{" "}
                </S.Item>
              </S.Produtora> */}

            </>
          )}
        </>
      )}
    </Layout>
  );
}

export default Cardezao;
