import React, { useState, useContext, useEffect } from "react";

import * as S from "./styles";

import { SwiperSlide } from "swiper/react";
import BaseScreen from "../../components/BaseScreen";
import HorizontalList from "../../components/HorizontalList";
import Card from "../../components/Card";
import Section from "../../components/Section";
import Processing from "../../components/Processing";
import CheckoutCard from "../../components/CheckoutCard";
// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'
import Store from "../../Connections/store";

import Channel from '../../Connections/channel';
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { useHistory } from "react-router-dom";
import lista from './lista'
import listaCanais from "./listaCard"
import firebase from "firebase";
import axios from "axios"
import { AuthContext } from "../../auth/AuthContext";
import useDadosList from "../../dados/hooks/listaHok";
import * as SCard from "../Canais/styles";


function Espetaculos() {
  const { currentUser } = useContext(AuthContext);
  const [ListaTeste, setListaTeste] = useState(lista);
  const ListaCanais = listaCanais


  const [horasAtual, sethorasAtual] = useState()
  const [mesAtual, setmesAtual] = useState()
  const [diasAtual, setdiasAtual] = useState()
  const [anoAtual, setanoAtual] = useState()
  const api = axios.create({
    baseURL: "https://json-serverepw.herokuapp.com/user",
  });


  const DataAtual = new Date()


  // localStorage.setItem("fav", []);
  // localStorage.setItem("assistidos", []);
  // localStorage.setItem("canaisFavoritos", []);

  const history = useHistory();
  // if(localStorage.getItem("fav") === undefined){
  //   localStorage.setItem("fav", [])
  // }
  useEffect(async () => {

    if (currentUser) {
      const userId =  firebase.auth().currentUser.uid;
      const { data } = await api.get("/" + userId)
    }

    setdiasAtual(DataAtual?.getDate())
    setanoAtual(DataAtual?.getFullYear())


    if (currentUser) {
      const userId = firebase.auth().currentUser.uid;
  
      async function fav() {
  
        const { data } = await api.get("/" + userId)
  
        console.log((data.fav))
        if (data.fav !== undefined) {
          localStorage.setItem("fav", JSON.stringify(data.fav))
        } else {
          localStorage.setItem("fav", []);
        }
        if (data.assistidos !== undefined) {
          localStorage.setItem("assistidos", JSON.stringify(data.assistidos));
        } else {
          localStorage.setItem("assistidos", []);
        }
        if (data.assistidos !== undefined) {
          localStorage.setItem("adquiridos", JSON.stringify(data.assistidos));
        } else {
          localStorage.setItem("adquiridos", []);
        }
        if (data.favCanais !== undefined) {
          localStorage.setItem("canaisFavoritos", JSON.stringify(data.favCanais));
        } else {
          localStorage.setItem("canaisFavoritos", []);
        }
      }
      fav()
    } else {
      history.push("/login")
    }


  }, [])



  function PaginaAssitir(dia, mes, ano, horas, videoID, videoID2, key) {

    if (parseInt(dia)+1 == diasAtual && mes == mesAtual && ano == anoAtual && horas == horasAtual) {
      alert("fechou")
      return
    }

    if (dia <= diasAtual && mes == mesAtual && ano == anoAtual)
      history.push(`/playespetaculo/${videoID}/${videoID2}/${key}`)
      window.location.reload()
  }


  return (
    <>
      <Header />

      <S.Container>
        <h2>Meus Espetáculos</h2>
        <Section title="Adquiridos" filter>
          <HorizontalList id="emAlta">
            {ListaTeste?.map((card, index) => {
              return (
                <SwiperSlide key={String(index)}>
                  {!localStorage.getItem("comprados").includes(card?.title) ? (
                    <>
                      <Card noContent />
                      {/* <S.A onClick={() => validacaoHorario(card)} > Assistir</S.A> */}
                    </>
                  ) : (
                    <S.CardTest>
                      <S.Img
                        src={card?.thumbnail}
                        style={{
                          position: "absolute",
                          width: 250,
                          height: 400,
                        }}
                        value={card?.key}
                      />

                      <S.InfoCard>
                        <S.InfoCardConteudo>
                          <S.Classificacao>
                            {card?.classificacao}
                          </S.Classificacao>
                          <S.Coracao>
                            <i className="bi bi-suit-heart-fill"></i>
                          </S.Coracao>

                          <S.Compartilhar>
                            <i className="bi bi-share-fill"></i>
                          </S.Compartilhar>
                        </S.InfoCardConteudo>

                        <S.Titulo>{card?.tituloEsp} </S.Titulo>
                        <S.Span size="20" cor="#FDCC12">
                          Expira em: {parseFloat(card?.valorHorarioSe?.dia) + 1}{" "}
                          / {card?.valorHorarioSe?.mes} /{" "}
                          {card?.valorHorarioSe?.ano} as{" "}
                          {card?.valorHorarioSe?.horas} Horas
                        </S.Span>
                        <S.ConteudoCard>Com {card?.artista}</S.ConteudoCard>
                        {/* <S.ConteudoCard>
                          <i class="bi bi-star-fill"></i>{" "}
                          <i class="bi bi-star-fill"></i>{" "}
                          <i class="bi bi-star-fill"></i>
                          <i class="bi bi-star-fill"></i>
                        </S.ConteudoCard> */}
                        <S.ConteudoCard>{card?.categoria}</S.ConteudoCard>
                        <S.Sinopsia style={{ overflow: "hidden" }}>
                          {card?.sinopsiaEsp}
                        </S.Sinopsia>

                        <S.ButtonAcessar
                          onClick={() => {
                            PaginaAssitir(
                              card?.valorHorarioSe?.dia,
                              card?.valorHorarioSe?.mes,
                              card?.valorHorarioSe?.ano,
                              card?.valorHorarioSe?.horas,
                              card?.videoID,
                              card?.videoID2,
                              card?.key
                            );
                          }}
                          className="btn btn-warning"
                        >
                          {" "}
                          Assistir{" "}
                        </S.ButtonAcessar>
                      </S.InfoCard>
                    </S.CardTest>
                  )}
                </SwiperSlide>
              );
            })}
          </HorizontalList>
        </Section>
        <Section title="Favoritos" filter>
          <HorizontalList id="emAlta">
            {ListaTeste?.map((card, index) => {
              const fav = localStorage.getItem("fav");
              return (
                <SwiperSlide key={String(index)}>
                  {!localStorage.getItem("fav").includes(card.title)? (
                    <Card noContent />
                  ) : (
                    <S.CardTest>
                      <S.Img
                        src={card?.thumbnail}
                        style={{
                          position: "absolute",
                          width: 250,
                          height: 400,
                        }}
                        value={card?.key}
                      />

                      <S.InfoCard>
                        <S.InfoCardConteudo>
                          <S.Classificacao>
                            {card?.classificacao}
                          </S.Classificacao>
                          <S.Coracao>
                            <i className="bi bi-suit-heart-fill"></i>
                          </S.Coracao>

                          <S.Compartilhar>
                            <i className="bi bi-share-fill"></i>
                          </S.Compartilhar>
                        </S.InfoCardConteudo>

                        <S.Titulo>{card?.title}</S.Titulo>
                        <S.ConteudoCard>Com {card?.Artista}</S.ConteudoCard>
                        {/* <S.ConteudoCard>
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>{" "}
                          <i className="bi bi-star-fill"></i>
                          <i className="bi bi-star-fill"></i>
                        </S.ConteudoCard> */}
                        <S.ConteudoCard>{card?.categoria}</S.ConteudoCard>
                        <S.Sinopsia style={{ overflow: "hidden" }}>
                          {card?.synopsis}
                        </S.Sinopsia>

                        <S.ButtonAcessar
                          className="btn btn-warning"
                          onClick={() => {
                            history.push(`/cardezao/${card?.key}`);
                            window.location.reload();
                          }}
                        >
                          Saiba Mais
                        </S.ButtonAcessar>
                      </S.InfoCard>
                    </S.CardTest>
                  )}
                </SwiperSlide>
              );
            })}
          </HorizontalList>
        </Section>
        <Section title="Assistidos" filter>
          <HorizontalList id="emAlta">
            {ListaTeste?.map((card, index) => {
              return (
                <SwiperSlide key={String(index)}>
                  {!localStorage.getItem("assistidos").includes(card?.title) ? (
                    <>
                      <Card noContent />
                      {/* <S.A onClick={() => validacaoHorario(card)} > Assistir</S.A> */}
                    </>
                  ) : (
                    <S.CardTest>
                      <S.Img
                        src={card?.thumbnail}
                        style={{
                          position: "absolute",
                          width: 250,
                          height: 400,
                        }}
                        value={card?.key}
                      />

                      <S.InfoCard>
                        <S.InfoCardConteudo>
                          <S.Classificacao>
                            {card?.classificacao}
                          </S.Classificacao>
                          <S.Coracao>
                            <i className="bi bi-suit-heart-fill"></i>
                          </S.Coracao>

                          <S.Compartilhar>
                            <i className="bi bi-share-fill"></i>
                          </S.Compartilhar>
                        </S.InfoCardConteudo>

                        <S.Titulo>{card?.tituloEsp} </S.Titulo>
                        <S.Span size="20" cor="#FDCC12">
                          Expira em: {parseFloat(card?.valorHorarioSe?.dia) + 1}{" "}
                          / {card?.valorHorarioSe?.mes} /{" "}
                          {card?.valorHorarioSe?.ano} as{" "}
                          {card?.valorHorarioSe?.horas} Horas
                        </S.Span>
                        <S.ConteudoCard>Com {card?.artista}</S.ConteudoCard>
                        {/* <S.ConteudoCard>
                          <i class="bi bi-star-fill"></i>{" "}
                          <i class="bi bi-star-fill"></i>{" "}
                          <i class="bi bi-star-fill"></i>
                          <i class="bi bi-star-fill"></i>
                        </S.ConteudoCard> */}
                        <S.ConteudoCard>{card?.categoria}</S.ConteudoCard>
                        <S.Sinopsia style={{ overflow: "hidden" }}>
                          {card?.sinopsiaEsp}
                        </S.Sinopsia>

                        <S.ButtonAcessar
                          onClick={() => {
                            PaginaAssitir(
                              card?.valorHorarioSe?.dia,
                              card?.valorHorarioSe?.mes,
                              card?.valorHorarioSe?.ano,
                              card?.valorHorarioSe?.horas,
                              card?.videoID,
                              card?.videoID2,
                              card?.key
                            );
                          }}
                          className="btn btn-warning"
                        >
                          {" "}
                          Assistir{" "}
                        </S.ButtonAcessar>
                      </S.InfoCard>
                    </S.CardTest>
                  )}
                </SwiperSlide>
              );
            })}
          </HorizontalList>
        </Section>
        <Section title="Canais Favoritos" filter>
          <HorizontalList id="canais">
            {ListaCanais.map((card, index) => {
              return (
                <SwiperSlide key={String(index)}>
                  {!localStorage
                    .getItem("canaisFavoritos")
                    .includes(card?.nome) || null ? (
                    <Card noContent />
                  ) : (
                    <SCard.CardCanais>
                      <SCard.Img
                        src={card?.thumbnail}
                        style={{
                          position: "absolute",
                          width: 250,
                          height: 250,
                          borderRadius: "50%",
                        }}
                        value={card?.key}
                        alt="imagem referente ao canal"
                      />
                      <SCard.InfoCard>
                        <SCard.Span size="18" style={{ marginBottom: "15px" }}>
                          <SCard.Coracao>
                            <i className="bi bi-suit-heart"></i>
                          </SCard.Coracao>
                        </SCard.Span>
                        <SCard.Span size="20">{card?.nome}</SCard.Span>

                        <SCard.Span size="10">
                          <i className="bi bi-circle-fill"></i>{" "}
                          <SCard.Span size="15">{card?.categoria}</SCard.Span>{" "}
                        </SCard.Span>
                        <SCard.Span></SCard.Span>
                        <SCard.verMais
                          size="18"
                          style={{ marginTop: "15px" }}
                          onClick={() => {
                            history.push(`/FichaCanal/${card?.key}`);
                            window.location.reload();
                          }}
                        >
                          Ver mais
                        </SCard.verMais>
                      </SCard.InfoCard>
                    </SCard.CardCanais>
                  )}
                </SwiperSlide>
              );
            })}
          </HorizontalList>
        </Section>
      </S.Container>
      <Footer />
    </>
  );
}

export default Espetaculos;
