import React, { useState, Component } from "react";


import * as S from "./styles";

// import RoundedImage from '../../components/RoundedImage';
// import image from '../../assets/clarisse.png'


import ListaHok from "../../dados/hooks/listaHok";
import StripeContainer from "../../components/Strip/StripContainer";
import useDadosList from "../../dados/hooks/listaHok";

import { useHistory } from "react-router-dom";
import {cpfMask} from "../../utils/utils.js"
import Layout from "../../layout";

function Pagamento () {

  
  const listaHok = ListaHok();
  const espetaculoUsuario = listaHok.listaEspetaculosDoUsuario;
  const [temItem, setTem] = useState(true)


  // const [documentId, setDocumentId] = useState("");
  const [cpf, setCpf] = useState("");
  const [nome, setNome] = useState("");

  

  function handleChangeNome(e){
    setNome( e.target.value )
  }
  
  function handleChangeCpf(e){
  
    const cpfMasked = cpfMask(e.target.value)

    setCpf( cpfMasked )
  }

  function virificar() {
    if (temItem) {
      setTem(false)
    } else {
      setTem(true)
    }
  }

  console.log(listaHok.listaPlanos)


  const history = useHistory()
  return (
    
      <Layout>
      <center>
      <S.Container>
        <S.Span size="40">Pagamento</S.Span> <br/>
        <center>
            <h1>  R$: {espetaculoUsuario.valorPreco ? espetaculoUsuario.valorPreco : listaHok.listaPlanos}</h1>
            <S.InputCardNumber placeholder="CPF" required value={ cpf} onChange={handleChangeCpf}/>
            <S.InputCardNumber placeholder="Nome" required value={nome} onChange={handleChangeNome}/> 
        </center>
        <StripeContainer nome={nome} cpf={cpf}/>
      </S.Container>
      </center>
      </Layout >
    
  );
}


export default Pagamento;
