import React, { useEffect, useState } from "react";

import { SwiperSlide } from "swiper/react";

import Header from "../../components/Header";
import Footer from "../../components/Footer";
import * as S from "./styles";

import Channel from "../../Connections/channel";

function Loja() {
  const channel = new Channel();

  const [cards, setCards] = useState([]);

  useEffect(() => {
    getContent();
  }, []);

  const getContent = async () => {
    const received = await channel.getContent();

    setCards(received.data);
  };

  return (
    <>
      <Header  />
              <S.Container>
                <S.Espaco>
                  <S.Span size="30">Em construção</S.Span>
                  <S.Span size="13">Aguarde novidades em breve!</S.Span>

                  <S.Img src={require('../../assets/emContrucao.png')}></S.Img>
                </S.Espaco>
                
              </S.Container>

      <Footer />
    </>
  );
}

export default Loja;
