import React, { useState } from "react";
import SwiperCore, {
  Navigation,
  Pagination,
  Autoplay,
  EffectFade,
  Controller,
} from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

import * as S from "./styles";

import banner1 from "../../assets/banner1.png";
import banner2 from "../../assets/banner-site.jpg";
// import banner3 from "../../assets/banner3.png";

SwiperCore.use([Navigation, Pagination, Autoplay, EffectFade, Controller]);

const Banner = ({ onSwiper, controller }) => {
  const [firstSwiper, setFirstSwiper] = useState(null);

  const slides = [
    <img src={banner1} height="inherit" alt="Imagem 1" />,
    <img src={banner2} height="inherit" alt="Imagem 2" />,
  ];

  return (
    <S.Container>
      <Swiper
        onSwiper={setFirstSwiper}
        controller={{ control: firstSwiper }}
        id="main"
        tag="section"
        speed={800}
        effect="fade"
        loop
        spaceBetween={0}
        slidesPerView={1}
        pagination={{ clickable: true }}
        // onSlideChange={() => console.log('slide change')}
        // onSwiper={(swiper) => console.log(swiper)}
        autoplay={{
          delay: 4500,
          disableOnInteraction: false,
        }}
        fadeEffect={{
          crossFade: true,
        }}
      >
        {slides.map((item, index)=> {
          return(
            <SwiperSlide key={String(index)}>
              {item}
            </SwiperSlide>
          )
        })}
        {/* <SwiperSlide>
            <img src={banner1} height="inherit" alt="Imagem 2" />,
          </SwiperSlide> */}
        <S.MainBanner></S.MainBanner>
      </Swiper>
    </S.Container>
  );
};

export default Banner;
